  transform: translate(-50%, 0);
  display: flex;
  min-width: 300px;
  width: fit-content;
  max-width: 600px;
  background: #202d40;
  /*opacity: 0;*/
  /*scroll-behavior: smooth;*/
  /*transition-duration: 1s*/

}
.loadingDiv{
  background: #e8e8e8;
  width: 100%;
  text-align: left;
  padding: 10px;
  z-index: 10;
}
.loadingDiv p:nth-child(2){
